import React from 'react'
import {
  Card,
  Form,
  Icon,
  Input,
  Button,
  Checkbox,
  notification,
  Modal,
} from 'antd'
import { Link } from 'react-router-dom'
import modal from '../../../common/components/widgets/Modal'

class LoginForm extends React.Component {
  constructor(props) {
    super(props)
    this.handleSubmit = this.handleSubmit.bind(this)
  }

  handleSubmit(e) {
    const { form, login, history } = this.props
    e.preventDefault()
    form.validateFieldsAndScroll(async (err, values) => {
      if (err) return

      const result = await login(values.email, values.password)
      if (result.success) {
        history.push('/')
      } else {
        notification.error({
          message: result.msg,
        })
      }
    })
  }

  handleShowForgotPasswordForm = values => {
    modal.show(
      <Card>
        Thng tin s c gi v {values.email}, vui lng kim tra Email ca
        bn.
      </Card>,
      {
        title: <b>QUN MT KHU</b>,
      },
    )
  }

  handleClickForgotPassword = () => {
    const { form } = this.props
    form.validateFields(['email'], (err, values) => {
      if (err) return
      Modal.confirm({
        content: 'Xc nhn qun mt khu',
        onOk: () => this.handleShowForgotPasswordForm(values),
      })
    })
  }

  render() {
    const { getFieldDecorator } = this.props.form
    return (
      <Card
        title={
          <Link to='/'>
            <img
              alt='logo'
              src={require('../../../assets/images/logo.png')}
              height={45}
            />
          </Link>
        }
      >
        <Form onSubmit={this.handleSubmit}>
          <Form.Item>
            {getFieldDecorator('email', {
              rules: [
                {
                  required: true,
                  message: 'Vui lng nhp Email ca bn!',
                  type: 'email',
                },
              ],
            })(
              <Input
                type='email'
                prefix={
                  <Icon type='user' style={{ color: 'rgba(0,0,0,.25)' }} />
                }
                placeholder='Email'
              />,
            )}
          </Form.Item>
          <Form.Item>
            {getFieldDecorator('password', {
              rules: [
                { required: true, message: 'Vui lng nhp mt khu ca bn!' },
              ],
            })(
              <Input
                prefix={
                  <Icon type='lock' style={{ color: 'rgba(0,0,0,.25)' }} />
                }
                type='password'
                placeholder='Password'
              />,
            )}
          </Form.Item>
          <Form.Item>
            {getFieldDecorator('remember', {
              valuePropName: 'checked',
              initialValue: true,
            })(<Checkbox>Ghi nh ti</Checkbox>)}
            <div
              onClick={this.handleClickForgotPassword}
              className='link'
              style={{ float: 'right' }}
            >
              Qun mt khu
            </div>
          </Form.Item>
          <Button type='primary' htmlType='submit' style={{ width: '100%' }}>
            Log in
          </Button>
        </Form>
      </Card>
    )
  }
}

export default Form.create({ name: 'normal_login' })(LoginForm)
