import React, { Component } from 'react'
import { Form, notification, message, Button, Icon, Input, Card } from 'antd'
import { changeUserPassword } from '../handlers'
import { handleError } from '../../../common/utils/handleError'
import Modal from '../../../common/components/widgets/Modal'

class ChangePasswordForm extends Component {
  handleSubmit = e => {
    e.preventDefault()
    const { form, getMe } = this.props
    form.validateFields(async (err, values) => {
      if (err) {
        message.error('Vui lng nhp  thng tin')
        return
      }

      try {
        await changeUserPassword(values)
        notification.success({ message: 'Thnh cng' })
        await getMe()
        Modal.hide()
      } catch (err) {
        handleError(err, form, notification)
      }
    })
  }

  compareConfirmPassword = async (rule, value) => {
    const password = this.props.form.getFieldValue('newPassword')
    if (password !== value) {
      throw new Error('Mt khu xc nhn khng khp')
    }
  }

  render() {
    const { form } = this.props
    return (
      <Card>
        <Form onSubmit={this.handleSubmit}>
          <Form.Item label='Mt khu c'>
            {form.getFieldDecorator('oldPassword', {
              rules: [
                {
                  required: true,
                  message: 'Vui lng nhp mt khu c',
                },
                {
                  pattern: '.{6,}',
                  message: 'Mt khu phi ln hn 6 k t',
                },
              ],
            })(
              <Input.Password
                autoComplete='false'
                placeholder='Nhp mt khu c'
              />,
            )}
          </Form.Item>
          <Form.Item label='Mt khu mi'>
            {form.getFieldDecorator('newPassword', {
              rules: [
                {
                  required: true,
                  message: 'Vui lng nhp mt khu mi',
                },
                {
                  pattern: '.{6,}',
                  message: 'Mt khu phi ln hn 6 k t',
                },
              ],
            })(
              <Input.Password
                autoComplete='false'
                placeholder='Nhp mt khu mi'
              />,
            )}
          </Form.Item>
          <Form.Item label='Nhp li mt khu mi'>
            {form.getFieldDecorator('confirmNewPassword', {
              rules: [
                {
                  validator: this.compareConfirmPassword,
                },
              ],
            })(
              <Input.Password
                autoComplete='false'
                placeholder='Nhp mt khu mi'
              />,
            )}
          </Form.Item>
          <div className='d-flex justify-content-end'>
            <Button onClick={() => Modal.hide()} style={{ marginRight: 10 }}>
              Hy
            </Button>{' '}
            <Button htmlType='submit' type='primary'>
              <Icon type='check' />
              Xc nhn
            </Button>
          </div>
        </Form>
      </Card>
    )
  }
}

export default Form.create()(ChangePasswordForm)
