import React, { Component } from 'react'
import CustomBreadcrumb from '../common/components/widgets/CustomBreadcrumb'
import MainLayout from '../common/hocs/MainLayout'
import { withRouter } from 'react-router-dom'
import {
  Card,
  Alert,
  Form,
  Input,
  Button,
  Icon,
  message,
  notification,
} from 'antd'
import { handleError } from '../common/utils/handleError'
import { changeUserPassword } from '../modules/user/handlers'
import { connect } from 'react-redux'
import userHandlers from '../modules/user/handlers'

class ChangePasswordPage extends Component {
  handleSubmit = e => {
    e.preventDefault()
    const { form, getMe } = this.props

    form.validateFields(async (err, values) => {
      if (err) {
        message.error('Vui lng nhp  thng tin')
        return
      }
      try {
        await changeUserPassword(values)
        notification.success({ message: 'Thnh cng' })
        await getMe()
      } catch (err) {
        handleError(err, form, notification)
      }
    })
  }

  compareConfirmPassword = async (rule, value) => {
    const password = this.props.form.getFieldValue('newPassword')
    if (password !== value) {
      throw new Error('Mt khu xc nhn khng khp')
    }
  }

  render() {
    const { form } = this.props

    return (
      <MainLayout mode={this.props.mode}>
        <CustomBreadcrumb
          items={[
            {
              url: '/admin/dashboard',
              icon: null,
              title: 'i mt khu',
            },
          ]}
        />
        <Card>
          <Alert
            type='warning'
            message='i mt khu'
            description=' m bo thng tin c nhn, vui lng i mt khu  tip tc!'
            showIcon
          ></Alert>
          <br />
          <Form onSubmit={this.handleSubmit}>
            {form.getFieldDecorator('oldPassword', {
              initialValue: '12345678',
            })(<Input hidden />)}
            <div className='row'>
              <Form.Item label='Mt khu mi' style={{ marginLeft: 15 }}>
                {form.getFieldDecorator('newPassword', {
                  rules: [
                    {
                      required: true,
                      message: 'Vui lng nhp mt khu mi',
                    },
                    {
                      pattern: '.{6,}',
                      message: 'Mt khu phi ln hn 6 k t',
                    },
                  ],
                })(
                  <Input.Password
                    autoComplete='false'
                    placeholder='Nhp mt khu mi'
                  />,
                )}
              </Form.Item>
              <Form.Item
                label='Nhp li mt khu mi'
                style={{ marginLeft: 15 }}
              >
                {form.getFieldDecorator('confirmNewPassword', {
                  rules: [
                    {
                      validator: this.compareConfirmPassword,
                    },
                  ],
                })(
                  <Input.Password
                    autoComplete='false'
                    placeholder='Nhp mt khu mi'
                  />,
                )}
              </Form.Item>
              <Form.Item style={{ marginLeft: 15, marginTop: 37 }}>
                <Button onClick={() => this.props.form.resetFields()}>
                  Reset
                </Button>
              </Form.Item>
              <Form.Item style={{ marginLeft: 15, marginTop: 37 }}>
                <Button htmlType='submit' type='primary'>
                  <Icon type='check' />
                  Xc nhn
                </Button>
              </Form.Item>
            </div>
          </Form>
        </Card>
      </MainLayout>
    )
  }
}

const mapDispatchToProps = userHandlers

export default connect(
  null,
  mapDispatchToProps,
)(Form.create()(withRouter(ChangePasswordPage)))
